import React, { useEffect, useState } from 'react';

import { Container, InfoProfile, IconAvatar } from './styles';

import imgProfileDefault from '../../assets/avatar-default.svg';

interface ProfileInfo {
  nameProfile: string;
  emailProfile: string;
  imgProfile?: string;
}

const Profile: React.FC = () => {
  const [profileInfo, setProfileInfo] = useState<ProfileInfo>(
    {} as ProfileInfo
  );

  useEffect(() => {
    const infoProfile = {
      emailProfile: 'teste@empresa.com',
      nameProfile: 'João da Silva',
      imgProfile: ''
      // 'https://summit.helphour.org/wp-content/uploads/2016/05/users-vector-icon-png_260862.jpg'
    };
    setProfileInfo(infoProfile);
  }, []);

  const { nameProfile, emailProfile, imgProfile } = profileInfo;

  return (
    <Container>
      <InfoProfile>
        <span>Bem vindo</span>
        <span>{nameProfile}</span>
        <span>{emailProfile}</span>
      </InfoProfile>
      <IconAvatar src={imgProfile || imgProfileDefault} />
    </Container>
  );
};

export default Profile;
