import styled from 'styled-components';

export const Container = styled.div`
  display: flex;
  flex-direction: column;
  max-width: 100vw;
  width: 100%;
  height: 100%;
  max-height: 100vh;
`;

export const Wrapper = styled.div`
  display: flex;
  height: 100%;
  max-height: 100vh;
  position: relative;
`;

export const Content = styled.div`
  z-index: 1;
  height: 100%;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 0 35px;
  max-width: 100vw;
`;
