import React, { MouseEvent, useCallback } from 'react';
import { Link, NavLink } from 'react-router-dom';
import { useMainContext } from '../../Context';
import Profile from '../Profile';

import {
  Container,
  Nav,
  WrapperText,
  CompanyIcon,
  ArrowIcon,
  QuotesIcon,
  AdminIcon,
  LogoutIcon,
  WrapperLink,
  WrapperProfile,
  BackMenuOpened
} from './styles';

const Menu: React.FC = () => {
  const { menuOpen, setMenuOpen } = useMainContext();

  const handleClickMenu = useCallback(
    (e: MouseEvent<HTMLElement>) => {
      // console.log(e.currentTarget.classList);
      setMenuOpen(!menuOpen);
    },
    [menuOpen]
  );

  return (
    <Container>
      <BackMenuOpened isMenuOpen={menuOpen} onClick={handleClickMenu} />
      <Nav className={menuOpen ? 'menu-open' : ''}>
        <ul>
          <li>
            <WrapperLink>
              <NavLink
                exact
                to="/RegisterCompany"
                activeClassName="active"
                onClick={handleClickMenu}
              >
                <WrapperText>
                  <CompanyIcon />
                  <span>Cadastro Empresa</span>
                </WrapperText>
                <div>
                  <ArrowIcon />
                </div>
              </NavLink>
            </WrapperLink>
          </li>
          <li>
            <WrapperLink>
              <NavLink
                exact
                to="/"
                activeClassName="active"
                onClick={handleClickMenu}
              >
                <WrapperText>
                  <QuotesIcon />
                  <span>Cotações</span>
                </WrapperText>
                <ArrowIcon />
              </NavLink>
            </WrapperLink>
          </li>

          <li>
            <WrapperLink className="admin">
              <NavLink
                exact
                to="/Admin"
                activeClassName="active"
                onClick={handleClickMenu}
              >
                <WrapperText>
                  <AdminIcon />
                  <span>Administrativo</span>
                </WrapperText>
                <ArrowIcon />
              </NavLink>
            </WrapperLink>
          </li>
          <li>
            <WrapperLink>
              <NavLink
                exact
                to="/Logout"
                activeClassName="active"
                onClick={handleClickMenu}
              >
                <WrapperText>
                  <LogoutIcon />
                  <span>Sair</span>
                </WrapperText>
                <ArrowIcon />
              </NavLink>
            </WrapperLink>
          </li>
        </ul>
        <WrapperProfile>
          <Profile />
        </WrapperProfile>
      </Nav>
    </Container>
  );
};
export default Menu;
