import React from 'react';
import { BrowserRouter } from 'react-router-dom';
import Routes from '../../Routes';
import Header from '../Header';
import Menu from '../Menu';

import { Container, Wrapper, Content } from './styles';

const Main: React.FC = () => (
  <Container>
    <Header />
    <Wrapper>
      <BrowserRouter>
        <Menu />
        <Content>
          <Routes />
        </Content>
      </BrowserRouter>
    </Wrapper>
  </Container>
);

export default Main;
