import React from 'react';
import { Link } from 'react-router-dom';

import {
  Container,
  Title,
  WrapperFilters,
  WrapperPanel,
  Panel
} from './styles';

const Quotes: React.FC = () => (
  <Container>
    <Title>Titulo</Title>
    <WrapperFilters />
    <WrapperPanel>
      <Link to="/quotesDetail/quoteId=1">
        <Panel>teste</Panel>
      </Link>
    </WrapperPanel>
  </Container>
);

export default Quotes;
