import React, { useEffect, useState, useCallback } from 'react';
import { useMainContext } from '../../Context';

import {
  Container,
  ButtonMenu,
  WrapperMenu,
  IconMenuOpened,
  LogoCompany,
  IconMenuClosed,
  WrapperProfile
} from './styles';
import Profile from '../Profile';

const Header: React.FC = () => {
  const { menuOpen, setMenuOpen } = useMainContext();
  const [company, setCompany] = useState('');

  useEffect(() => {
    setCompany('lakatos');
  }, []);

  const handleMenu = useCallback(() => {
    setMenuOpen(!menuOpen);
  }, [menuOpen]);

  return (
    <Container>
      <WrapperMenu>
        <ButtonMenu type="button" onClick={handleMenu}>
          <IconMenuClosed className={menuOpen ? 'open' : ''} />
          <IconMenuOpened className={!menuOpen ? 'open' : ''} />
        </ButtonMenu>
        <LogoCompany src={`images/${company}.svg`} />
      </WrapperMenu>
      <WrapperProfile>
        <Profile />
      </WrapperProfile>
    </Container>
  );
};

export default Header;
