export const WrapperLink = styled.div`
  display: flex;
  //padding: 20px 20px;
  width: 100%;
  max-width: 100%;
  align-items: center;

  a {
    font-size: 14px;
    display: flex;
    text-decoration: none;
    display: flex;
    width: 100%;
    max-width: 100%;
    padding: 20px 10px 20px 15px;
    align-items: center;
    position: relative;

    ::after {
      content: '';
      position: absolute;
      right: 0px;
      width: 2px;
      height: 59px;
      border: 1px solid transparent;
      background-color: var(--primary);
      transform: scaleY(0);
    }

    :hover {
      background-color: var(--background);
      transform: translateY(-1px);
      transition: all 0.3s;
      ::after {
        transition: all 0.3s ease-in;
        transform: scaleY(1);
      }
    }
    &.active {
      background-color: var(--background);
      :hover {
        transition: all 0.3s;
        transform: translateY(-1px);
      }
    }
    &.active::after {
      transform: scaleY(1);
      content: '';
      position: absolute;
      right: 0px;
      width: 2px;
      height: 59px;
      border: 1px solid transparent;
      background-color: var(--primary);
    }
  }

  &.admin {
    border-top: 1px solid #f5f6fa;
    border-bottom: 1px solid #f5f6fa;
  }
`;

