import React, { createContext, useState, useEffect, useCallback } from 'react';

interface IMainContext {
  menuOpen: boolean;
  setMenuOpen: (value: boolean) => void;
}

const Context = createContext<IMainContext>({} as IMainContext);

export const ContextProvider: React.FC = ({ children }) => {
  const [menuOpen, setMenuOpen] = useState(false);

  const handleSetMenuOpen = useCallback((value: boolean) => {
    localStorage.setItem('menu-open', JSON.stringify(value));
    setMenuOpen(value);
  }, []);

  useEffect(() => {
    const getMenuOpenInStorage = localStorage.getItem('menu-open');
    if (getMenuOpenInStorage) {
      setMenuOpen(JSON.parse(getMenuOpenInStorage));
    } else {
      setMenuOpen(false);
    }
  }, []);

  return (
    <Context.Provider value={{ menuOpen, setMenuOpen: handleSetMenuOpen }}>
      {children}
    </Context.Provider>
  );
};

export const useMainContext = () => React.useContext(Context);
