import styled from 'styled-components';

export const Container = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
`;

export const InfoProfile = styled.div`
  display: flex;
  flex-direction: column;
  text-align: right;
  width: 100%;

  span {
    font-size: 12px;
    color: var(--blue-light);
    text-transform: capitalize;
  }
  span:nth-child(2) {
    font-size: 16px;
    color: var(--primary);
    text-transform: capitalize;
  }
  span:nth-child(3) {
    font-size: 10px;
    color: #326d8f;
    text-transform: lowercase;
  }
`;

export const IconAvatar = styled.img`
  height: 45px;
  width: 45px;
  margin: 13px 15px;
  border-radius: 50%;
  display: flex;
  align-items: center;
`;
