import React from 'react';

import { Route, Switch, Redirect, RouteComponentProps } from 'react-router-dom';
import QuotesDetail from '../Pages/QuotesDetail';
import Quotes from '../Pages/Quotes';
import RegisterCompany from '../Pages/RegisterCompany';
import Admin from '../Pages/Admin';
import Logout from '../Pages/Logout';
import Login from '../Pages/Login';
import { isAuthenticated } from '../Auth';

const PrivateRoute: React.FC<{ path: string }> = ({ children, path }) => (
  <Route path={path}>
    {isAuthenticated() ? <>{children}</> : <Redirect to="/login" />}
    {/* {isAuthenticated() ? <>{children}</> : <h1>tem que se logar</h1>} */}
  </Route>
);

const Routes: React.FC = () => (
  <Switch>
    <Route exact path="/">
      <Quotes />
    </Route>
    <Route path="/quotesDetail/:quoteId">
      <QuotesDetail />
    </Route>
    <Route path="/RegisterCompany">
      <RegisterCompany />
    </Route>
    <PrivateRoute path="/Admin">
      <Admin />
    </PrivateRoute>
    <Route path="/Logout">
      <Logout />
    </Route>
    <Route path="/Login">
      <Login />
    </Route>
    <Route path="*">
      <h1>Página não encontrada</h1>
    </Route>
    {/* <PrivateRoute path="/app" component={() => Quotes} /> */}
  </Switch>
);

// const Routes: React.FC = () => <h1>rota</h1>;

export default Routes;
